// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";

.list-index {
 background:#0a2743 !important;
 padding: 20px 0;
 min-height: 100vh;
 
 .list__page{
	text-align: center;
	margin: 30px 0 0;
	list-style: none;
	padding: 0;

	li {
	 margin: 0 0 5px;
	 a {
		color:#fff;
		font-size: 25px;
		line-height: 30px;
	 }
	}
 }
 h1{
	margin: 0;
	color:#fff !important;
	text-align: center;
 }
} //end .page-list-index
html {
	@media (min-width: 1024px) {
		overflow: hidden;
	}
}
body {
 background: #132326 url($img_dir+"bg.jpg") no-repeat center;
	background-size: cover;
	background-attachment: fixed; 
	
	@media (min-width: 1024px) {
 	height: 100vh;
	}	
}
#scene {
	height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
	position: fixed;
	z-index: 2;	

	&::before {
		@extend .before;
		top: 0;
		left: 0;
		height: 100%;
		background: rgba($color-black,.1);
		z-index: 1;		
		transition: opacity .2s;
		opacity: 0;
		width: 48%;

		@media (min-width: 1024px) {
			width: (741px*100vw)/1600px;
		}
	}	
}
.layer {
	z-index: 2;
}
#specks {
	position: absolute;
	z-index: 999;
	height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
}
.speck {
  opacity: 0;
  height: 4px;
  width: 4px;
  background: url('../images/speck.webp');
  background-size: cover;
  position: absolute;
  border-radius: 10px;
  z-index: 99;
}

.img {
  position: absolute;
  height: 100000px;
  width: 100000px;
  top: -8500px;
  left: -8000px;
  animation-timing-function: cubic-bezier(0.250, 0.250, 0.750, 0.750);
  animation-name: float;
  animation-iteration-count: infinite;
}

#img-1 {
  opacity: 0.25;
  background: url($img_dir+"dust1.jpg") repeat;
  animation-duration: 600s;
}

#img-2 {
  opacity: 0.2;
  background: url($img_dir+"dust2.jpg") repeat;
  animation-duration: 450s;
}

#img-3 {
  opacity: 0.15;
  background: url($img_dir+"dust3.jpg") repeat;
  animation-duration: 350s;
}
/*ANIMATIONS*/

@keyframes twinkle {
  0% {
    transform: translate(0px, 0px);
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0.75;
  }
  70% {
    opacity: 0;
  }
  100% {
    transform: translate(175px, 100px);
  }
}

@keyframes float {
  0% {
    transform: translate(0px, 0px);
  }
  100% {
      transform:translate(8000px, 4000px);
  }
}
.wrapper {
	position: relative;	
	z-index: 10;

	@media (min-width: 1024px) {
		overflow: hidden;
		height: 100vh;
	}
}

header {
 @extend .d-block; 

 @media (min-width: 1024px) {
	 position: absolute;
	 left: 0;
		top: (77px*57.25vw)/916px;
 }
 @media (min-width: 1601px) {
 	top: 20px;
 }
	z-index: 15;
}
.container {
	margin: 0 auto;
	width: 100%;
	@media (max-width: 413px) {
		padding: 0 15px !important;
	}

	@media (min-width: 1024px) {
		width: 84vw;
	}
}
.header-content, .footer-content {
	@extend .d-block;
	position: relative;
}
.header-left-box {
	float: left;
}
.logo {
	width: 106px;
	float: left;
	margin-top: 8px;
	max-width: 171px;

	@media (min-width: 1024px) {
		margin-top: 0;
		position: absolute;
		left: 0;
		top: (8px*57.25vw)/916px;
		width: (171px*100vw)/1600px;
	}
}
.nav-list {
	display: flex;
	float: left;

	@media (min-width: 1024px) {
		margin-left: (256px*100vw)/1600px;
	}

	&.nav-header {
		margin-top: (15px*57.25vw)/916px;

		@media (min-width: 1601px) {
			margin-top: 15px;
		}
	}
	&.nav-footer {
		margin-top: (13px*57.25vw)/916px;
	}

	li {
		opacity: 0;

		&:first-child {
			@media (min-width: 1024px) {
				width: (466px*100vw)/1600px;
			}
		}

		a {
			white-space: nowrap;
			position: relative;
			overflow: hidden;
			display: inline-block;
			color: $color-white;
			font-family: $font-family-pfdin-thin;
			font-size: 15px;
			line-height: 15px;
			
			@media (min-width: 1024px) {
				font-size: (16px*57.25vw)/916px;
				line-height: (19px*57.25vw)/916px;
			}

			@media (min-width: 1601px) {
				font-size: 16px;
				line-height: 19px;
			}

			span {
				display: inline-block;
				position: relative;   
				transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;     
				padding: 0 0 2px;

				&::before {
					content: attr(data-hover);
					position: absolute;
					top: 110%;
					transform: translate3d(0px, 0px, 0px);  
				}
			}

			&:hover {
				span {
					transform: translateY(-100%);
				}
			}

		}

		&.is-active {
			a {
					border-bottom: 1px solid $color-blue;
				
				@media (min-width: 1024px) {
					border-bottom: (2px*57.25vw)/916px solid $color-blue;
				}

				@media (min-width: 1601px) {
					border-bottom: 2px solid $color-blue;
				}

			}
		}
	}
}
.header-right-box {
	float: right;
}
.btn-nav {
	background: $color-white;
	border-radius: 50%;
	text-decoration: none !important;
	display: flex;
	align-items: center;
	justify-content: center; 
	border: 1px solid $color-white;
	transition: background 0.3s;
	position: relative;
	width: 38px;
	height: 38px;

	@media (min-width: 1024px) {
		width: (54px*100vw)/1600px;
		height: (54px*57.25vw)/916px;
	}

	@media (min-width: 1601px) {
		width: 54px;
		height: 54px;
	}

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		width: 16.5px;
		height: 15px;
		margin-top: -(15px/2);
		margin-left: -(16.5px/2);

		transition: opacity 0.3s;
		background: url($img_dir+"sprites/svg/burger.svg") no-repeat;
		background-size: contain !important;
		
		@media (min-width: 1024px) {
			width: (23px*100vw)/1600px;
			height: (20px*57.25vw)/916px;
			margin-top: -((20px*57.25vw)/916px)/2;
			margin-left: -((23px*100vw)/1600px)/2;
		}

		@media (min-width: 1601px) {
			width: 23px;
			height: 20px;
			margin-top: -10px;
			margin-left: -11.5px;			
		}

	}
	&::after {
		opacity: 0;
		background: url($img_dir+"sprites/svg/burger-white.svg") no-repeat;
	}

		&:hover {
		background: transparent;
		&::before {
			opacity: 0;
		}
		&::after {
			opacity: 1;
		}
	}
}
.header-lang {
	display: block;
	position: relative;
	margin-top: 18px;
	@media (min-width: 1024px) {
		margin-top: (26px*57.25vw)/916px;
	}

	@media (min-width: 1601px) {
		margin-top: 26px;
	}
}
.header-lang-btn {
	background: rgba($color-white,.05);
	border-radius: 50%;
	text-decoration: none !important;
	display: flex;
	align-items: center;
	justify-content: center; 
	transition: background 0.3s;
	position: relative;	
	text-decoration: none !important;
	width: 38px;
	height: 38px;
	
	@media (min-width: 1024px) {
		width: (54px*100vw)/1600px;
		height: (54px*57.25vw)/916px;
	}	

	@media (min-width: 1601px) {
		width: 54px;
		height: 54px;
	}

	&:hover {
		background: rgba($color-white,.3);
	}

	img {
		border-radius: 50%;
		width: 15px;
		height: 15px;

		@media (min-width: 1024px) {
			width: (21px*100vw)/1600px;
			height: (21px*57.25vw)/916px;
		}

		@media (min-width: 1601px) {
			width: 21px;
			height: 21px;
		}
	}

}
.header-lang-panel {
	position: absolute;
	top: 0;
	display: flex;
	// background: rgba($color-white,.05);
	background: rgba($color-grey,.9);
	width: 0;
	overflow: hidden;
	align-items: center;
	justify-content: space-between;
	transition: all 0.3s;
	opacity: 0;
	right: 40px;
	height: 38px;
	border-radius: 19px;

	a {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
	}

	@media (min-width: 1024px) {
		height: (54px*57.25vw)/916px;
		border-radius: (27px*100vw)/1600px;
		right: (58px*100vw)/1600px;
	}

	@media (min-width: 1601px) {
		height: 54px;
		border-radius: 27px;
		right: 58px;
	}
	
	li {
		height: 100%;
		width: 50%;

		img {
			border-radius: 50%;
			width: 15px;
			height: 15px;

			@media (min-width: 1024px) {
				width: (21px*100vw)/1600px;
				height: (21px*57.25vw)/916px;
			}

			@media (min-width: 1601px) {
				width: 21px;
				height: 21px;
			}
		}		

		&:last-child {
			margin-right: 0;
		}
	}

	&.is-active {
		opacity: 1;
		width: 100px;

		@media (min-width: 1024px) {
			width: (100px*100vw)/1600px;
		}

		@media (min-width: 1601px) {
			width: 100px;
		}
	}
}
.content-page {
	display: block;
	position: relative;
	z-index: 10;
	@media (min-width: 1024px) {
		height: 100vh;
	}
}
#wrapper-home {
	.content-page {
		@media (min-width: 1024px) {

			$homeContainerHeight: (231px*57.25vw)/916px;
			// display: flex;
			// align-items: center;
			.container {
				height: $homeContainerHeight;
				position: relative;
				top: 50%;
				margin-top: -($homeContainerHeight/2);
			}
		}
	}
}
.main-logo {
	display: block;
	max-width: 755px;

	@media (min-width: 1024px) {
		margin: 0 auto;
		width: (755px*100vw)/1600px;
		// margin-left: (256px*100vw)/1600px;
	}
}
.main-logo-content {
	display: flex;
	width: 100%;
}
.main-logo-left {
	display: flex;
	flex-direction: column;
}
.logo-part-1, 
.logo-part-2,
.logo-part-3,
.logo-part-4 {
	opacity: 0;   
}
.logo-part-1 {
	-webkit-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
	-webkit-transform: translateX(-50%);
	-moz-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);  		
}
.logo-part-2 {
	-webkit-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
	-webkit-transform: translateY(50px);
	-moz-transform: translateY(50px);
	-ms-transform: translateY(50px);
	transform: translateY(50px);  		
}
.logo-part-3 {
	-webkit-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
	-webkit-transform: translateY(-60%);
	-moz-transform: translateY(-60%);
	-ms-transform: translateY(-60%);
	transform: translateY(-60%);  		
}
.logo-part-4 {
	-webkit-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
	-webkit-transform: translateX(50%);
	-moz-transform: translateX(50%);
	-ms-transform: translateX(50%);
	transform: translateX(50%);  		
}




footer {
 @extend .d-block; 
	z-index: 15;
	@media (min-width: 1024px) {
		bottom: (91px*57.25vw)/916px;
 	left: 0;
 	position: fixed;
	}

	@media (min-width: 1601px) {
		bottom: 30px;
	}
}
.nav-arrow {
	display: flex;
	float: right;
	align-items: center;

	li {
		margin-left: 20px;

		@media (min-width: 1024px) {
			margin-left: (28px*100vw)/1600px;
		}

		@media (min-width: 1601px) {
			margin-left: 28px;
		}

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba($color-white, .1);
			border-radius: 50%;
			transition: background 0.3s;
			position: relative;
			border: 1px solid transparent;
			transition: background 0.3s;
			width: 38px;
			height: 38px;

			@media (min-width: 1024px) {
				width: (54px*100vw)/1600px;
				height: (54px*57.25vw)/916px;			
			}

			@media (min-width: 1601px) {
				width: 54px;
				height: 54px;			
			}

			&.is-active {
				background: $color-white !important;

			}
			&:hover {
				background: transparent !important;
				border-color: $color-white;

				&::before {
					opacity: 0;
				}

			&::after {
					opacity: 1;
				}

			}

		}

		&:first-child {
			margin-left: 0;
		}

	}
}

.btn-arrow-top, .btn-arrow-down {
	&::before, &::after {
		@extend .before;
		left: 50%;
		top: 50%;
		background-size: contain !important;
		transition: opacity 0.3s;
		width: 17px;
		height: 10px;
		margin-left: -8.5px;
		margin-top: -5px;

		@media (min-width: 1024px) {
			width: (25px*100vw)/1600px;
			height: (14px*57.25vw)/916px;
			margin-left: -((25px*100vw)/1600px)/2;
			margin-top: -((14px*57.25vw)/916px)/2;
		}

		@media (min-width: 1601px) {
			width: 25px;
			height: 14px;
			margin-left: -12.5px;
			margin-top: -7px;
		}

	}
}
.btn-arrow-top {
	&::before {
		background: url($img_dir+"sprites/png/icon-arrow-top.png") no-repeat;
	}
	&::after {
		opacity: 0;
		background: url($img_dir+"sprites/png/icon-arrow-top-white.png") no-repeat;
	}
}
.btn-arrow-down {
	&::before {
		background: url($img_dir+"sprites/png/icon-arrow-down.png") no-repeat;
	}
	&::after {
		opacity: 0;
		background: url($img_dir+"sprites/png/icon-arrow-down-white.png") no-repeat;
	}	
}

@-webkit-keyframes fadeOutBody {
	0% {
	 opacity: 1;
	}

	100% {
	 opacity: 0;
	}
}

@keyframes fadeOutBody {
	0% {
	 opacity: 1;
	}

	100% {
	 opacity: 0;
	}
}
.fadeOutBody {
	-webkit-animation-duration: 1s;
	-moz-animation-duration: 1s;
	-ms-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	-moz-animation-fill-mode: both;
	-ms-animation-fill-mode: both;
	animation-fill-mode: both;      
	-webkit-animation-name: fadeOutBody;
	-moz-animation-name: fadeOutBody;
	-ms-animation-name: fadeOutBody;
	animation-name: fadeOutBody;
}
.effect-1, .effect-2 {
	opacity: 0;
	-webkit-animation-fill-mode: both;
	-moz-animation-fill-mode: both;
	-ms-animation-fill-mode: both;
	animation-fill-mode: both;     
}
.effect-1 {
	-webkit-animation-duration: .4s;
	-moz-animation-duration: .4s;
	-ms-animation-duration: .4s;
	animation-duration: .4s;  
}
.effect-2 {
	-webkit-animation-duration: .6s;
	-moz-animation-duration: .6s;
	-ms-animation-duration: .6s;
	animation-duration: .6s;      
}
.effect-3 {
	-webkit-transform: translateY(50%);
	-moz-transform: translateY(50%);
	-ms-transform: translateY(50%);
	transform: translateY(50%); 
}
.effectText {
	-webkit-transform: translateY(50%);
	-moz-transform: translateY(50%);
	-ms-transform: translateY(50%);
	transform: translateY(50%); 
}
.effect-4 {
	-webkit-transform: translateY(-50%);
	-moz-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);     
}
.effectTitle {
	-webkit-transform: translateY(-50%);
	-moz-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);     
}
.effect-3, .effect-4, .effectText, .effectTitle {
		opacity: 0;    
	-webkit-transition: transform .3s, opacity .5s;
	-moz-transition: transform .3s, opacity .5s;
	-ms-transition: transform .3s, opacity .5s;
	transition: transform .3s, opacity .5s; 
}


@-webkit-keyframes zoomIn {
	0% {
	 opacity: 0;
	 -webkit-transform: scale3d(.3, .3, .3);
	 -moz-transform: scale3d(.3, .3, .3);
	 -ms-transform: scale3d(.3, .3, .3);
	 transform: scale3d(.3, .3, .3);
	}

	50% {
	 opacity: 1;
	}
}

@keyframes zoomIn {
	0% {
	 opacity: 0;
	 -webkit-transform: scale3d(.3, .3, .3);
	 -moz-transform: scale3d(.3, .3, .3);
	 -ms-transform: scale3d(.3, .3, .3);
	 transform: scale3d(.3, .3, .3);
	}

	50% {
	 opacity: 1;
	}
}
.link-1, .link-3 {
	@media (min-width: 1024px) {
		-webkit-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
	}   
}
.link-2, .link-4 {
	@media (min-width: 1024px) {
	-webkit-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;     
	}
}
.link-1 {
	@media (min-width: 1024px) {
		-webkit-transform: translateY(-30px);
		-moz-transform: translateY(-30px);
		-ms-transform: translateY(-30px);
		transform: translateY(-30px);   	
	}
}
.link-2 {
	@media (min-width: 1024px) {
	-webkit-transform: translateY(-60px);
	-moz-transform: translateY(-60px);
	-ms-transform: translateY(-60px);
	transform: translateY(-60px);      		
	}
}
.link-3 {  
	@media (min-width: 1024px) {
	-webkit-transform: translateY(30px);
	-moz-transform: translateY(30px);
	-ms-transform: translateY(30px);
	transform: translateY(30px);      
	}  
}
.link-4 {  
	@media (min-width: 1024px) {
	-webkit-transform: translateY(60px);
	-moz-transform: translateY(60px);
	-ms-transform: translateY(60px);
	transform: translateY(60px);      
	}
}
.menu__container {
	position: absolute;
	overflow: hidden;
	background: rgba($color-green,.7);
	z-index: 100;
	display: none;
	left: 0;
	bottom: 0;
	top: 0;
	width: 100%;
	height: 100vh;
	min-height: 100vh;
}
.menu__box {
	position: absolute;
	height: 100%;
	display: flex;
	align-items: center;
	top: 0;
	overflow-x: hidden;
	overflow-y: auto;
	background: rgba($color-green2,.8);    
	-webkit-transform: translateZ(0px);
	-moz-transform: translateZ(0px);
	-ms-transform: translateZ(0px);
	transform: translateZ(0px);    
