.closeMenu {
	background: $color-white;
	border-radius: 50%;
	text-decoration: none !important;
	display: flex;
	align-items: center;
	justify-content: center; 
	border: 1px solid $color-white;
	transition: background 0.2s;
	position: absolute;
	width: 38px;
	height: 38px;
	right: 25px;
	top: 30px;
	cursor: pointer;
	-webkit-transform: translateZ(0px);
	-moz-transform: translateZ(0px);
	-ms-transform: translateZ(0px);
	transform: translateZ(0px);    
	-webkit-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-moz-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	-ms-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
	transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;   
	z-index: 10;

	@media (min-width: 1024px) {
		width: (54px*100vw)/1600px;
		height: (54px*57.25vw)/916px;
		right: 8vw;
		top: (77px*57.25vw)/916px;
	}

	@media (min-width: 1601px) {
		width: 54px;
		height: 54px;
		top: 20px;
	}	


	&::before, &::after {
		@extend .before;
		background: $color-green;
		left: 50%;
		top: 50%;
		transition: background 0.2s;
		width: 18px;
		height: 3px;
		border-radius: 1.5px;
		margin-top: -1.5px;
		margin-left: -9px;

		@media (min-width: 1024px) {
			border-radius: (2.5px*100vw)/1600px;
			height: (4px*57.25vw)/916px;
			width: (28px*100vw)/1600px;
			margin-top: -((2px*57.25vw)/916px);
			margin-left: -((14px*100vw)/1600px);
		}

		@media (min-width: 1601px) {
			border-radius: 2.5px;
			height: 4px;
			width: 28px;
			margin-top: -2px;
			margin-left: -14px;			
		}
	}
	&::before {
		-webkit-transform: translateY(0px) rotate(40deg);
		-moz-transform: translateY(0px) rotate(40deg);
		-ms-transform: translateY(0px) rotate(40deg);
		transform: translateY(0px) rotate(40deg);      
	}
	&::after {
		transform: translateY(0px) rotate(-40deg);
		-webkit-transform: translateY(0px) rotate(-40deg);
		-moz-transform: translateY(0px) rotate(-40deg);
		-ms-transform: translateY(0px) rotate(-40deg);
		transform: translateY(0px) rotate(-40deg);       
	}

		&:hover {
			background: transparent;
		&::before, &::after {
			background: $color-white;
		}
	}	
	
}

