		float: left;
	}
	

	&::before {
		@extend .before;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: $color-grey;
		opacity: .5;
		transition: opacity 0.3s;
	}

}
.slide-title {
	display: table-cell;
	position: absolute;
	left: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	color: $color-white;
	font-family: $font-family-pfdin-thin;
	font-size: 15px;
	line-height: 15px;
