/* START #{$mediaWidth2} */ //-1023px
@media ($feature: $mediaWidth2) {

	html, body {

		&.portrait {
			height: 100%;
			overflow: hidden;

			.wrapper {
				height: 100%;
				min-height: 100%;
			}			
		} //- portrait


		&.landscape { //-&.is-scroll
				height: auto !important;
				overflow-y: auto !important;	

			.wrapper {
				height: auto !important;
			}
		
		} //- landscape


		&.is-scroll {
			height: auto !important;
			overflow: auto !important;	
			min-height: 100%;
			flex: 1;
			display: flex;			

			.wrapper {
				min-height: 100% !important;
				height: auto !important;
			}			
		}
		

	}//- html, body



	.portrait {

		#wrapper-home {
			.content-page {
				height: calc(100% - 188px);

				.container {
					height: 100%;
					// justify-content: center;
				}
			}
		}//- #wrapper-home

		#wrapper-partnership {
			min-height: 100%;

			.content-page {
				height: calc(100% - 206px);
				min-height: calc(100% - 206px);

				.container {
					height: 100%;
				}
			}
			.partnership-content {
				height: 100%;
			}
			.partnership-text {
				height: calc(100% - 236px);
			}
			.scroll-pane {
				height: calc(100% - 66px);
				overflow: hidden;
			}

		}//- wrapper-partnership

		#wrapper-media {
			.content-page {
				height: calc(100% - 206px);

				.container {
					height: 100%;
				}
			}
		}//- wrapper-media


		#wrapper-history {
			min-height: 100%;

			.content-page {
				height: calc(100% - 222px); //- Ð´Ð¾ 400
				min-height: calc(100% - 222px);

				.container {
					height: 100%;
				}
			}

			.history-content {
				height: 100%;
			}
			.partnership-text {
				height: calc(100% - 77px); //- Ð´Ð¾ 610
			}
			.history-tab-content {
				height: calc(100% - 37px);
			}
			.scroll-pane {
				height: calc(100% - 62px);
				// height: calc(100% - 66px);
				overflow: hidden;
			}

		}//- wrapper-history

	} //- portrait

	.landscape {
		.media-container {
			margin-bottom: 20px;
		}
	} //- landscape

	.is-scroll {
		
		#wrapper-partnership {
			.content-page {
				height: auto !important;

				.container {
					height: auto !important;
				}
			}
			.partnership-content {
				height: auto !important;
			}
			.partnership-text {
				height: auto !important;
			}
			.scroll-pane {
				height: auto !important;
				overflow: visible !important;
			}

		}//- wrapper-partnership


		#wrapper-history {

			.content-page {
				height: auto !important;

				.container {
					height: auto !important;
				}
			}

			.history-content {
				height: auto !important;
			}
			.partnership-text {
				height: auto !important; //- Ð´Ð¾ 610
			}
			.history-tab-content {
				height: auto !important;
			}
			.scroll-pane {
				height: auto !important;
				overflow: visible !important;
			}

		}//- wrapper-history		

		

		
	} //- .is-scroll
	
	.history-tab-content {
			width: 100%;
			float: left;		
	}

		.content-page {
			width: 100%;
			float: left;
	}

	.wrapper {
		display: block;
		float: left;
		width: 100%;
	}




	.xs-hidden {
		display: none !important;
	}	
	.xs-visible {
		display: block !important;
	}
	.htmlFix {
	 overflow-x: hidden !important;
	 overflow-y: hidden !important;
	}
	// body, html {
	//  height: auto !important;
	// }
	body {
		-webkit-animation: -amp-start 8s steps(1, end) 0s 1 normal both;
		-moz-animation: -amp-start 8s steps(1, end) 0s 1 normal both;
		-o-animation: -amp-start 8s steps(1, end) 0s 1 normal both;
		animation: -amp-start 8s steps(1, end) 0s 1 normal both; 		
	}
	.navFix {
	 position: fixed !important;
	 visibility: visible;
	 -webkit-animation: none 0s ease 0s 1 normal none running;
	 -moz-animation: none 0s ease 0s 1 normal none running;
	 -o-animation: none 0s ease 0s 1 normal none running;
	 animation: none 0s ease 0s 1 normal none running;
	 overflow: visible !important;
	}	
	.container {
		padding: 0 25px 0 30px;
		float: left;
		width: 100%;
	}
	header {
		margin-top: 30px;
	}

	
	footer {
		height: 64px;
		// padding-bottom: 26px;
	}
	.main-logo {
		width: 100%;
		max-width: 360px;
	}
	.nav-list-mob {
		float: left;
		width: 100%;
		max-width: 360px;
		margin-top: 73px;
	}
	.main-logo {
		margin-top: 63px;
	}
	.nav-list {
		display: block;
		width: 100%;
