		li {
			display: block;
			width: 100%;
			float: left;			
			margin-bottom: 36px;
			a {
				font-size: 15px;
			}
		}
	}


.link-1 {
		-webkit-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;  

		-webkit-transform: translateX(-30px);
		-moz-transform: translateX(-30px);
		-ms-transform: translateX(-30px);
		transform: translateX(-30px);   	
}
.link-2 {
		-webkit-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;  

	-webkit-transform: translateX(-60px);
	-moz-transform: translateX(-60px);
	-ms-transform: translateX(-60px);
	transform: translateX(-60px);      		
}
.link-3 {  
		-webkit-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) 0s;  

	-webkit-transform: translateX(-90px);
	-moz-transform: translateX(-90px);
	-ms-transform: translateX(-90px);
	transform: translateX(-90px);      
}
.link-4 {  
		-webkit-transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.7s cubic-bezier(0.23, 1, 0.32, 1) 0s;  

	-webkit-transform: translateX(-120px);
	-moz-transform: translateX(-120px);
	-ms-transform: translateX(-120px);
	transform: translateX(-120px);      
}

	#wrapper-home {
		.container {
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}
	.partnership-img {
		display: block;
		width: 100%;
		float: left;
		margin-bottom: 16px;

		img {
			width: 100%;
			max-width: 229px;
			display: block;
			margin: 0 auto;
		}
	}
	.partnership-text {
		display: block;
		width: 100%;
		float: left;		
	}
	.title br {
		display: none;
	}
	.partnership-text {
		p {
			display: none;

			&:nth-child(1),
			&:nth-child(2) {
				display: inline-block;
			}

			&:nth-child(3) {
				display: inline-block;
				overflow: hidden;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 2; 								
			}

		}
	}

	.effect-5 {
		opacity: 0;
		-webkit-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-moz-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		-ms-transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;
		transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1) 0s;  
		-webkit-transform: translateX(-80px);
		-moz-transform: translateX(-80px);
		-ms-transform: translateX(-80px);
		transform: translateX(-80px);  
	}

	.box-action {
		width: 100%;
		position: relative;
		float: left;
		margin-top: 5px;
		margin-bottom: 20px;

		&::before {
			content: "";
			display: block;
			position: absolute;
			left: -30px;
			top: 0;
			height: 38px;
			width: 7px;
			border-radius: 0 5px 5px 0;
			background: $color-red;
		}
		&.is-hidden {
			display: none !important;
		}
	}
	.btn-see-more {
		text-decoration: none !important;
		font: 14px/38px $font-family-pfdin-thin;
		display: inline-block;
		float: left;
		color: $color-white;
	}
	
	.header-left-box {
		width: calc(100% - 38px);
	}
	.page-title-box {
		width: 100%;
		position: relative;
		float: left;
		padding-right: 25px;
		margin-top: 16px;
		margin-bottom: 18px;

		&::before {
			content: "";
			display: block;
			position: absolute;
			left: -30px;
			top: 0;
			height: 38px;
			width: 7px;
			border-radius: 0 5px 5px 0;
			background: $color-blue2;
		}
		&.is-hidden {
			display: none !important;
		}
	}
	.page-title {
		color: $color-white;
		font: 20px/26px $font-family-pfdin-thin;
		min-height: 38px;
		display: flex;
		align-items: center;
	}
	.slider-actions {
		display: block;
		width: 100%;
		float: left;		
	}

	.slide-box, .swiper-slide {
		width: 245px !important;
	}

	#wrapper-media {
		.content-page {
			display: flex;
			flex-direction: column;
		}
	}
	.media-content {
		flex: 1;
	}
	.media-container {
		margin-top: 14px;
	}
	.box-action-media {
		&::before {
			left: 0 !important;
		}
	}
	.slider-arrows{
		float: left;
		width: 100%;
		justify-content: center;
	}

.sliderPhotoVideo {
	.swiper-slide-active {
		.slide-img::before { opacity: 0; }

		.slide-title {
				opacity: 1;
			-webkit-transform: translateY(60px);
			-moz-transform: translateY(60px);
			-ms-transform: translateY(60px);
			transform: translateY(60px);										
		}
	}
}	
	.page-title-history {
		display: flex;
		justify-content: space-between;
		padding-right: 15px;

		.page-title {
			line-height: 22px;
			margin-top: -3px;
		}
	}
	.history-sub-title {
		position: relative;
		margin-left: 33px;
		max-width: 285px;
		top: -4px;

		br {
			display: none;
		}
	}
	.history-awards {
		li {
			display: flex;
			max-width: 50%;
			min-width: 50%;
		}
	}
	.awards-box {
		display: flex;
		min-width: 50%;
		max-width: 50%;
		flex-direction: column;
	}
	.awards-text {
		text-align: center;
	}
 .history-right-box {
 	margin-bottom: 19px;
 }
 .history-tab-list {
 	margin-bottom: 21px;
 	display: flex;
 	justify-content: space-between;

 	li {
 		margin-right: 0;
 	}
 }
