	//- loading
	.loading {
		.nav-list li {
			-webkit-transform: translateX(0);
			-moz-transform: translateX(0);
			-ms-transform: translateX(0);
			transform: translateX(0);			
		}

		.effect-5 {
			opacity: 1;
			-webkit-transform: translateX(0);
			-moz-transform: translateX(0);
			-ms-transform: translateX(0);
			transform: translateX(0);						
		}
	} //-loading
	.btn-arrow-top::after,
	.btn-arrow-down::after  {
		display: none;
	}
	.btn-arrow-top::before, .btn-arrow-down::before {
		left: 50%;
		top: 50%;		
		width: 10px;
		height: 17px;
		margin-left: -6px;
		margin-top: -8.5px;
		background-size: contain !important;		
	}
	.btn-arrow-down::before {
		margin-left: -4px;
	}
	.btn-arrow-top::before {
		background: url($img_dir+"sprites/png/icon-arrow-left-dark.png") no-repeat;
	}
	.btn-arrow-down::before {
		background: url($img_dir+"sprites/png/icon-arrow-right-dark.png") no-repeat;
	}
}/* END #{$mediaWidth2} */	//-1023px

